import React, { useState } from 'react';
import { ActivityIndicator, Text, View, Image, Button, TouchableWithoutFeedback, FlatList} from 'react-native';
import data from './assets/cards.json';
import SmallSetImage from './SmallSetImage';
import SoundButton from './SoundButton';
import CardListTab from './CardListTab';
import FlatListItemSeparator from './FlatListItemSeparator';
import cardsets from './assets/cardsets.json';
import { Audio } from 'expo-av';
import { NavigationContainer, useScrollToTop } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { NavigationEvents } from "react-navigation";
import { Toolbar } from 'react-native-material-ui';

const Tab = createBottomTabNavigator();
const Stack = createStackNavigator();

const App = () => {
  return (
    <NavigationContainer>
      <Stack.Navigator>
        <Stack.Screen name="Main" component={TabScreen} />
      </Stack.Navigator>
    </NavigationContainer>
  )
}

const TabScreen = ({ navigation }) => {

  React.useLayoutEffect(() => {
    navigation.setOptions({
      headerShown: false,
      headerRight: () => (
        <Button onPress={() => setCount(c => c + 1)} title="Update count" />
      ),
    });
  }, [navigation]);

  return (

    <Tab.Navigator>
      <Tab.Screen name="SetList" component={SetList}/>
      <Tab.Screen
        name="Cards"
        component={CardListTab}
        initialParams = {{cardSet: null}}

         />
    </Tab.Navigator>

  )
}

const SetList = ({ navigation }) => {
  const cardSets = cardsets.filter(x => x.craftable === 1)

  return (
    <>
    <Toolbar
            //leftElement="menu"
            centerElement="HearthSFX"
          />
    <View style={{ flex: 1, padding: 8 }}>
      <FlatList
        data={cardSets}
        renderItem={({ item }) => (
          <TouchableWithoutFeedback onPress= {e => {
              navigation.navigate('Cards', {
                cardSet: item.set
              })
            }
          }>
          <View style={{ flex: 1, flexDirection: 'row', alignItems: 'center'
          , justifyContent: 'space-between'
          , marginTop: 4, marginBottom: 4, marginRight: 8}}>
            <SmallSetImage cardSet={item.set}/>
            <Text style={{flexGrow: 1}}>{item.name}</Text>
          </View>
          </TouchableWithoutFeedback>
        )}
        ItemSeparatorComponent={FlatListItemSeparator}
      />
    </View>
    </>
  );
}



export default App;
